<template>
	<aside class="sidebar transition" :class="{'collapse': sidebarStatus}">
    <div class="sidebar-wrap">
      <div class="sidebar-header">
        <a href="/" class="brand ellipsis" :title="title">
          <i class="fa fa-flag fa-lg"></i>
          <strong>{{ title }}</strong>
        </a>
      </div>
      <div class="divide-line"></div>
  		<el-menu 
        router 
        theme="dark" 
        :default-active="$route.path" 
        :unique-opened="true" 
        :collapse="sidebarStatus">
        <el-submenu :index="index+''" v-for="(menu, index) in routes" :key="menu.id">
          <template slot="title">
            <i class="fa fa-lg" :class="menu.icon"></i>
            <span slot="title">{{ menu.name }}</span>
          </template>
            <el-menu-item :index="submenu.index" v-for="submenu in menu.children" :key="submenu.id">
            	<router-link :to="submenu.link">{{ submenu.name }}</router-link>
            </el-menu-item>
        </el-submenu>
      </el-menu>
    </div>
	</aside>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      title: '悦视觉用户管理系统',
      routes: [
        {
          name: '我的账户',
          icon: 'fa-user',
          children: [
            {
              name: '账户中心', link: '/account/infocenter', index: '/account/infocenter'
            },
            {
              name: '账号设置', link: '/account/settings', index: '/account/settings'
            },
            {
              name: '注册我的企业', link: '/account/registedpartner', index: '/account/registedpartner'
            },
          ]
        },
        {
          name: '我的企业管理',
          icon: 'fa-home',
          children: [
            // {
            //   name: '企业信息', link: '/partner/info', index: '/partner/info'
            // },
            {
              name: '企业信息编辑', link: '/partner/infoedit', index: '/partner/infoedit'
            },
            {
              name: '组织部门管理', link: '/partner/organizemanage', index: '/partner/organizemanage'
            },
            // {
            //   name: '员工管理', link: '/partner/staffManage', index: '/partner/staffManage'
            // },
            {
              name: '职位管理', link: '/partner/titlemanage', index: '/partner/titlemanage'
            },
          ]
        },
        {
          name: '我的商家',
          icon: 'fa-sitemap',
          children: [
            {
              name: '商家审核', link: '/admin/partnerverify', index: '/admin/partnerverify'
            },
            {
              name: '商家管理', link: '/admin/partnermanage', index: '/admin/partnermanage'
            },
            {
              name: '合同管理', link: '/admin/contractmanage', index: '/admin/contractmanage'
            },
          ]
        },
        {
          name: '系统管理',
          icon: 'fa-cog',
          children: [
            {
              name: '功能管理', link: '/system/funcmanage', index: '/system/funcmanage'
            },
            {
              name: '角色管理', link: '/system/rolemanage', index: '/system/rolemanage'
            },
            // {
            //   name: '账户管理', link: '/system/accountmanage', index: '/system/accountmanage'
            // },
            {
              name: '商家类型管理', link: '/system/partnertypemanage', index: '/system/partnertypemanage'
            },
            {
              name: '合同模板管理', link: '/system/templatemanage', index: '/system/templatemanage'
            },
          ]
        },
      ],
      isCollapse: false
    }
  },
  computed: {
    ...mapGetters([
      'sidebarStatus'
    ]),
  },
}
</script>
<style scoped lang="scss">
	.sidebar {
		position: fixed;
		top: 0;
    left: 0;
    z-index: 99;
		width: 240px;
		height: 100%;
		background-color: #324157;
    &.collapse {
      width: 64px;
    }
	}
  .sidebar-wrap {
    position: absolute;
    width: 240px;
    height: 100%;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }
  .sidebar-header {
    padding: 20px 15px;
    text-align: left;
    .brand {
      margin: 10px 0;
      font-size: 16px;
      line-height: 1.1;
      color: #DFE4ED;
    }
  }
  .divide-line {
    width: 100%;
    height: 2px;
    overflow: hidden;
    font-size: 0;
    background-color: #233b4c;
  }
  .el-submenu {
    text-align: left;
    .el-menu-item {
      padding-left: 10px
    }
  }
	.el-menu-item {
    text-indent: 5px;
		a {
			display: block;
			text-decoration: none;
			color: #bfcbd9;
		}
		.router-link-active {
			color: #fff;
		}
	}
	.el-submenu .fa {
		vertical-align: baseline;
    margin-right: 10px;
	}
  .transition {
    transition: all .1s
  }
</style>



