<template>
	<div id="layout">
		<AsideComp></AsideComp>
    <HeaderComp></HeaderComp>
    <keep-alive>
      <MainComp></MainComp>
    </keep-alive>
	</div>
</template>
<script>
import AsideComp from './Aside'
import HeaderComp from './Header'
import MainComp from './Main'
import { getMyInfo } from '@/api'
export default {
  name: 'layout',
  components: {
    AsideComp,
    HeaderComp,
    MainComp
  },
  methods: {
    // 获取用户信息
    getUserInfo() {
      getMyInfo().then(res => {
        if(res.data.code === '0001') {
          this.$store.dispatch('saveUserInfo', res.data.result.userInfo)
        } else {
          this.$message.error(res.data.message)
        }
      }).catch(err => {
        console.log(err)
        this.$catchError(err)
      })
    },
  },
  mounted() {
    this.getUserInfo()
  }
}
</script>



