<template>
	<header class="header clearfix transition" :class="{'expand': sidebarStatus}">
		<div class="pull-left">
			<el-button type="primary" class="btn-toggle" @click="toggleSidebar"><i class="fa fa-bars"></i></el-button>
		</div>
		<div class="pull-right">
			<el-button type="primary" class="m-r" @click="enterMall">进入商城</el-button>
			<el-dropdown :hide-on-click="false" trigger="click">
			  <span class="el-dropdown-link">
			  	<img v-if="userInfo.avatar" :src="userInfo.avatar" class="avatar"/>
			  	<img v-else src="../assets/img/avatar.gif" alt="头像" class="avatar" />
			    {{ userInfo.name ? userInfo.name : userInfo.email}}<i class="el-icon-caret-bottom el-icon--right"></i>
			  </span>
			  <el-dropdown-menu slot="dropdown">
			    <el-dropdown-item>
			    	<router-link to="/account/infocenter">账户中心</router-link>
			    </el-dropdown-item>
			    <el-dropdown-item>
			    	<router-link to="/account/settings">修改密码</router-link>
			    </el-dropdown-item>
			    <el-dropdown-item disabled>
			    	<a @click="enterB2B">进入交易系统</a>
			    </el-dropdown-item>
					<el-dropdown-item divided>
						<a @click="logout">退出</a>
					</el-dropdown-item>
			  </el-dropdown-menu>
			</el-dropdown>
		</div>
	</header>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { requestExit } from '@/api'
export default {
	data () {
		return {
		}
	},
  methods: {
  	...mapActions([
  		'toggleSidebar'
  	]),
  	enterMall() {
  		window.open('https://transformgit.github.io/yueshijue/')
  	},
  	enterB2B() {
  		window.open('https://transformgit.github.io/yueadmin-b2b/')
  	},
  	// 退出
    logout () {
    	this.$confirm('确定退出系统?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
      	requestExit().then(res => {
      		console.log(res)
      		if(res.data.code === '0001') {
      			this.$router.replace('/login')
      			this.$message('已退出登录')
      		} else {
      			this.$message.error(res.data.message)
      		}
      	})
      }).catch(err => {
      	console.log(err)
        this.$message('已取消操作');          
      })
    }
  },
  computed: {
	  ...mapGetters([
  		'sidebarStatus',
  		'userInfo'
  	])
  },
}
</script>
<style scoped lang="scss">
	.header {
		position: fixed;
		top: 0;
		left: 240px;
		right: 0;
		z-index: 999;
		background: #fff;
		box-shadow: 0 0 1px rgba(0,0,0,0.25);
		&.expand {
			left: 0;
		}
	}
	.el-menu {
		border-radius: 0;
		background-color: rgba(255,255,255,.95);
	}
	.avatar {
		width: 20px;
		height: 20px;
		vertical-align: middle
	}
	.el-dropdown {
		min-width: 150px;
		height: 60px;
		line-height: 60px;
	}
	.el-dropdown-link {
		padding: 10px;
		border-radius: 4px;
    border: 1px solid #f0f0f8;
    cursor: pointer;
	}
	.el-dropdown-menu {
		width: 150px;
		a {
			display: block
		}
	}
	.btn-toggle {
		margin: 12px 20px;
	}
</style>



